*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
}

body {
    font-family: "Lato", sans-serif;
    font-weight: 400;
    line-height: 1.7;
    color: $color-grey-dark;
}
a,
a:visited,
a:link {
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
main {
  max-width: 770px;
  margin: 0 auto;
}
header {
    text-align: center;
    margin: 20px 0;
}